

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.card {
    background: #fff;
    border-radius: 2px;
    height: 300px;
    margin: 1rem;
    position: relative;
    width: 300px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
  }
  .card > .img-container {
    height: 100%;
    overflow: hidden; 
    text-align: center;
    background:rgb(235, 19, 19);
  }
  .card > .img-container > img {
    width: 275px;
    height: 275px;
    margin-top: .75rem;
    margin-bottom: .75rem;
  }
  
  .card > .content {
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 15px;
  }
  
  .card > .content > ul {
    list-style-type: none;
  }
  
  .card > .content > ul > li {
    margin-top: 10px;
  }
.wrapper {
    padding-top: 50px;
    /* background: url(https://www.indiewire.com/wp-content/uploads/2014/12/pee-wees-playhouse.jpg);
    background-repeat: no-repeat;
    background-size: cover; */
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    padding: 20px;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
  }

  .score{
    color: #2a38b9;
  }
.title {
    width: 100%;
    text-align: center;
    font-size: 2rem;
    text-decoration: underline;
    color: rgb(0, 0, 167);
    margin-bottom: 50px;
    font-family: 'Carter One', cursive;
  }

  .score {
    font-family: 'Carter One', cursive;
  }
  
